import { Alert, Box, Button, Container, Link, TextField, Typography } from '@mui/material';
import { useNavigate } from 'react-router-dom';
import { useState } from 'react';
import { auth, signInWithEmailAndPassword } from '../firebase';
import { startSession } from '../session';
import { handleFirebaseAuthError } from '../functions/firebaseAuthErrorHandler';
import GoogleAuthButton from '../components/GoogleAuthButton';

export default function Login() {
  const navigate = useNavigate();
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');

  const onSubmit = async (event) => {
    event.preventDefault();

    // validate the inputs
    if (!email || !password) {
      setError(",  email  !");
      return;
    }

    // clear the errors
    setError("");

    try {
      let loginResponse = await signInWithEmailAndPassword(auth, email, password);
      startSession(loginResponse.user);
      navigate("/user");
    } catch (error) {
      console.error(error.message);
      setError(handleFirebaseAuthError(error));
    }
  };


  return (
    <Container maxWidth="xs" sx={{ mt: 2 }}>
      <Typography variant="h5" component="h1" gutterBottom textAlign="center">
        
      </Typography>
      {error && <Alert severity="error" sx={{ my: 2 }}>{error}</Alert>}
      <Box component="form" onSubmit={onSubmit}>
        <TextField
          label="Email"
          variant="outlined"
          autoComplete="email"
          value={email}
          onChange={(e) => setEmail(e.target.value)}
          sx={{ mt: 1 }}
          fullWidth
        />
        <TextField
          label=""
          variant="outlined"
          type="password"
          autoComplete="new-password"
          value={password}
          onChange={(e) => setPassword(e.target.value)}
          sx={{ mt: 3 }}
          fullWidth
        />
        <Button variant="contained" type="submit" sx={{ mt: 3 }} fullWidth>
          
        </Button>
        <Box sx={{ mt: 2 }}>
            ? <Link href="/register">!</Link>
        </Box>
        <Box sx={{ mt: 2 }}>
          <GoogleAuthButton />
        </Box>
      </Box>
    </Container>
  );
}