// firebaseAuthErrorHandler.js
export const handleFirebaseAuthError = (error) => {
 switch (error.code) {
    case 'auth/claims-too-large':
      return '  ,   setCustomUserClaims(),      1000 .';
    case 'auth/email-already-exists':
      return ' email    .      email.';
    case 'auth/id-token-expired':
      return ' Firebase ID- .';
    case 'auth/id-token-revoked':
      return 'Firebase ID-  .';
    case 'auth/insufficient-permission':
      return ' ,    Admin SDK,          .    "  Firebase"     ,             Admin SDK.';
    case 'auth/internal-error':
      return '         .         ,   .   ,          .';
    case 'auth/invalid-argument':
      return '     .       .';
    case 'auth/invalid-claims':
      return '     setCustomUserClaims() .';
    case 'auth/invalid-continue-uri':
      return 'URL     URL-.';
    case 'auth/invalid-creation-time':
      return '       UTC.';
    case 'auth/invalid-credential':
      return '   !';
    case 'auth/invalid-disabled-field':
      return '      .     .';
    case 'auth/invalid-display-name':
      return '    displayName  .     .';
    case 'auth/invalid-dynamic-link-domain':
      return '           .';
    case 'auth/invalid-email':
      return '    email  .       .';
    case 'auth/invalid-email-verified':
      return '    emailVerified  .     .';
    case 'auth/invalid-hash-algorithm':
      return '          .';
    case 'auth/invalid-hash-block-size':
      return '      .';
    case 'auth/invalid-hash-derived-key-length':
      return ' ,   ,    .';
    case 'auth/invalid-hash-key':
      return '      .';
    case 'auth/invalid-hash-memory-cost':
      return '      .';
    case 'auth/invalid-hash-parallelization':
      return '     .';
    case 'auth/invalid-hash-rounds':
      return '     .';
    case 'auth/invalid-hash-salt-separator':
      return '         .';
    case 'auth/invalid-id-token':
      return ' ID-     Firebase.';
    case 'auth/invalid-last-sign-in-time':
      return '          UTC.';
    case 'auth/invalid-page-token':
      return '     listUsers() .      .';
    case 'auth/invalid-password':
      return '      .          .';
    case 'auth/invalid-password-hash':
      return '      .';
    case 'auth/invalid-password-salt':
      return '      .';
    case 'auth/invalid-phone-number':
      return '   phoneNumber .     ,   E.164.';
    case 'auth/invalid-photo-url':
      return '    photoURL  .     URL.';
    case 'auth/invalid-provider-data':
      return 'providerData      UserInfo.';
    case 'auth/weak-password':
      return ' .      6 ';
     case 'auth is not defined':
      return '  ! ';
    default:
      if(error.message == 'auth is not defined'){
        return '  ! '
      }else{
        return error.message;
      }
  }
};
